import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';
import { compose } from 'redux';
import { createStructuredSelector } from 'reselect';
import renderHTML from 'react-render-html';
import { Grid } from '@material-ui/core';

import injectReducer from '../../../utils/injectReducer';
import injectSaga from '../../../utils/injectSaga';

import reducer from '../reducer';
import saga from '../saga';

import Loading from '../../../components/commons/Loading';
import { makeSelectQuestionPage } from '../selectors';
import { getQuestionPage } from '../actions';
import { questionPageId } from '../../../configs';

class Question extends Component {
    componentDidMount() {
        this.getPages();
    }

    getPages = async () => {
        const { getQuestionPage } = this.props;
        getQuestionPage(questionPageId);
    };

    render() {
        const { questionPage } = this.props;
        if (questionPage.waiting) {
            return (
                <div className="loading-wrp">
                    <Loading size={22} />
                </div>
            );
        }

        if (questionPage.data) {
            return (
                <Grid container className="page">
                    <Helmet>
                        <title>Liên hệ</title>
                        <meta
                            name="description"
                            content="Xitashop.com: Trang liên hệ"
                        />
                        <link rel="canonical" href={window.location.href} />
                    </Helmet>
                    <div className="page-wrp">
                        {renderHTML(questionPage.data.content)}
                    </div>
                </Grid>
            );
        } else {
            return null;
        }
    }
}

Question.propTypes = {
    questionPage: PropTypes.object.isRequired,
    getQuestionPage: PropTypes.func.isRequired
};

Question.defaultProps = {};

const mapStateToProps = createStructuredSelector({
    questionPage: makeSelectQuestionPage()
});

const mapDispatchToProps = {
    getQuestionPage
};

const withConnect = connect(
    mapStateToProps,
    mapDispatchToProps
);

const withReducer = injectReducer({ key: 'pagesReducer', reducer });
const withSaga = injectSaga({ key: 'pagesSaga', saga });

export default compose(
    withReducer,
    withSaga,
    withConnect
)(Question);
