import initialState from './initialState';
import * as consts from './consts';

// Handles post related actions
export default function(state = initialState, action) {
    switch (action.type) {
        case consts.GET_CONTACT_PAGE_REQUEST:
            return state
                .setIn(['contactPage', 'data'], false)
                .setIn(['contactPage', 'waiting'], true)
                .setIn(['contactPage', 'error'], false);
        case consts.GET_CONTACT_PAGE_SUCCESS:
            return state
                .setIn(['contactPage', 'data'], action.contactPage)
                .setIn(['contactPage', 'waiting'], false)
                .setIn(['contactPage', 'error'], false);
        case consts.GET_CONTACT_PAGE_ERROR:
            return state
                .setIn(['contactPage', 'data'], false)
                .setIn(['contactPage', 'waiting'], false)
                .setIn(['contactPage', 'error'], action.error);
        case consts.GET_QUESTION_PAGE_REQUEST:
            return state
                .setIn(['questionPage', 'data'], false)
                .setIn(['questionPage', 'waiting'], true)
                .setIn(['questionPage', 'error'], false);
        case consts.GET_QUESTION_PAGE_SUCCESS:
            return state
                .setIn(['questionPage', 'data'], action.questionPage)
                .setIn(['questionPage', 'waiting'], false)
                .setIn(['questionPage', 'error'], false);
        case consts.GET_QUESTION_PAGE_ERROR:
            return state
                .setIn(['questionPage', 'data'], false)
                .setIn(['questionPage', 'waiting'], false)
                .setIn(['questionPage', 'error'], action.error);
        default:
            return state;
    }
}
