import { put, call, all, takeLatest } from 'redux-saga/effects';
import services from '../../services/bloggerAPIs';
import * as consts from './consts';

function* getContactPageSaga({ payload }) {
    try {
        const contactPage = yield call(services.getPage, payload);
        yield all([
            put({
                type: consts.GET_CONTACT_PAGE_SUCCESS,
                contactPage: contactPage.data
            })
        ]);
    } catch (error) {
        yield put({
            type: consts.GET_CONTACT_PAGE_ERROR,
            error: error.response.data
        });
    }
}

function* getPromotionPageSaga({ payload }) {
    try {
        const questionPage = yield call(services.getPage, payload);
        yield all([
            put({
                type: consts.GET_QUESTION_PAGE_SUCCESS,
                questionPage: questionPage.data
            })
        ]);
    } catch (error) {
        yield put({
            type: consts.GET_QUESTION_PAGE_ERROR,
            error: error.response.data
        });
    }
}

export default function* root() {
    yield takeLatest(consts.GET_CONTACT_PAGE_REQUEST, getContactPageSaga);
    yield takeLatest(consts.GET_QUESTION_PAGE_REQUEST, getPromotionPageSaga);
}
