import axios from 'axios';

import { limitPerPage, apiKey, blogId, limitLatest } from '../configs';

const getEndpoint = payload => {
    return `https://www.googleapis.com/blogger/v3/blogs/${blogId}/posts?labels=${
        payload.label
    }&key=${apiKey}${payload.limit ? '&maxResults=' + payload.limit : ''}`;
};

function instanceCreate() {
    return axios.create({});
}

function getPosts(payload) {
    const endpoint = getEndpoint(payload, null);
    const apiInstance = instanceCreate();
    return apiInstance.get(endpoint);
}

function getSelectedPost(payload) {
    const endpoint = `https://www.googleapis.com/blogger/v3/blogs/${blogId}/posts/${payload}?key=${apiKey}`;
    const apiInstance = instanceCreate();
    return apiInstance.get(endpoint);
}

function getPage(payload) {
    const endpoint = `https://www.googleapis.com/blogger/v3/blogs/${blogId}/pages/${payload}?key=${apiKey}`;
    const apiInstance = instanceCreate();
    return apiInstance.get(endpoint);
}

function getHomePosts(payload) {
    let endpoint = `https://www.googleapis.com/blogger/v3/blogs/${blogId}/posts?key=${apiKey}&maxResults=${limitPerPage}`;
    if (payload) {
        endpoint = `https://www.googleapis.com/blogger/v3/blogs/${blogId}/${
            payload.keyword
                ? 'posts/search?q=' + payload.keyword + '&'
                : 'posts?'
        }key=${apiKey}&maxResults=${limitPerPage}${
            payload.pageToken ? '&pageToken=' + payload.pageToken : ''
        }`;
    }
    const apiInstance = instanceCreate();
    return apiInstance.get(endpoint);
}

function getLatestPost() {
    const endpoint = `https://www.googleapis.com/blogger/v3/blogs/${blogId}/posts?maxResults=${limitLatest}&orderBy=published&key=${apiKey}`;
    const apiInstance = instanceCreate();
    return apiInstance.get(endpoint);
}

export default {
    getHomePosts,
    getPosts,
    getSelectedPost,
    getLatestPost,
    getPage
};
