import { createSelector } from 'reselect';
import initialState from './initialState';

const pagesSelector = state => state.get('pagesReducer', initialState);

const makeSelectQuestionPage = () =>
    createSelector(
        pagesSelector,
        state => state.get('questionPage').toJS()
    );

const makeSelectContactPage = () =>
    createSelector(
        pagesSelector,
        state => state.get('contactPage').toJS()
    );

export { makeSelectContactPage, makeSelectQuestionPage };
